import { useState, useEffect } from 'react';
import { getAllTokenIds, getCardDetails } from '../utils/web3Utils';
import CardDisplay from './CardDisplay';

export default function MyCards({ account, refreshTrigger }) {
  const [cards, setCards] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');

  useEffect(() => {
    if (account) {
      loadCards();
    }
  }, [account, refreshTrigger]);

  const loadCards = async () => {
    if (!account) return;

    setLoading(true);
    setError('');

    try {
      // Petit d√©lai pour s'assurer que la blockchain a finalis√© la transaction
      await new Promise(resolve => setTimeout(resolve, 500));
      
      const tokenIds = await getAllTokenIds(account);
      console.log(`üìå getAllTokenIds retourn√©: ${tokenIds.length} cartes`);
      
      if (tokenIds.length === 0) {
        setCards([]);
        setLoading(false);
        return;
      }
      
      // Charger les d√©tails de chaque carte via cards(tokenId)
      const cardsData = await Promise.all(
        tokenIds.map((tokenId) => getCardDetails(tokenId))
      );
      
      const validCards = cardsData.filter(Boolean);
      console.log(`üé¥ Cartes charg√©es: ${validCards.length}/${tokenIds.length}`);
      setCards(validCards);
    } catch (error) {
      console.error("Erreur lors du chargement des cartes:", error);
      setError("Impossible de charger vos cartes");
    } finally {
      setLoading(false);
    }
  };

  if (!account) {
    return (
      <div className="my-cards-section">
        <div className="alert alert-info">
          Connectez votre wallet pour voir vos cartes
        </div>
      </div>
    );
  }

  if (loading) {
    return (
      <div className="my-cards-section">
        <div className="loading">
          <div className="spinner"></div>
          <p>Chargement de vos cartes...</p>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="my-cards-section">
        <div className="alert alert-error">
          {error}
        </div>
        <button onClick={loadCards} className="btn btn-secondary">
          R√©essayer
        </button>
      </div>
    );
  }

  if (cards.length === 0) {
    return (
      <div className="my-cards-section">
        <div className="empty-state">
          <h2>üöÄ Aucune carte pour le moment</h2>
          <p>Mintez votre premi√®re carte pour commencer votre collection!</p>
        </div>
      </div>
    );
  }

  return (
    <div className="my-cards-section">
      <div className="section-header">
        <div>
          <h2>üé¥ Ma Collection</h2>
          <p>{cards.length} carte{cards.length > 1 ? 's' : ''}</p>
        </div>
        <button onClick={loadCards} className="btn btn-refresh">
          üîÑ Rafra√Æchir
        </button>
      </div>

      <div className="cards-grid">
        {cards.map((cardData) => (
          <CardDisplay
            key={cardData.tokenId}
            tokenId={cardData.tokenId}
            card={cardData}
          />
        ))}
      </div>
    </div>
  );
}
